import * as axios from "axios"

export const dataAPI = {
    getFeedBack() {
        return axios.get("http://localhost:3000/test-task/feedback_data.json")
            .then(response => {
                return response.data;
            })
    },

    getBlogPost() {
        return axios.get("http://localhost:3000/test-task/blog_posts.json")
            .then(response => {
                return response.data;
            })
    }
}